@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  font-family: Inter , sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

button {
  background: rgb(3, 3, 3);
  border: none;
  border-radius: 3px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  font-weight: 800;
  height: 3.2em;
  letter-spacing: 4px;
  text-transform: uppercase;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
  width: 22rem;
}

button:disabled {
  background: white;
  color: black;
  opacity: 0.6;
}

button:hover {
  opacity: 0.9;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

button:disabled:hover {
  -webkit-transform: none;
          transform: none;
}

.login__container {
  align-items: center;
  background: -webkit-gradient(linear, left bottom, left top, from(rgb(195, 20, 50)), to(rgb(196, 94, 68)));
  background: linear-gradient(to top, rgb(195, 20, 50), rgb(196, 94, 68));
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.login {
  align-items: center;
  -webkit-backdrop-filter: blur(4.1px);
          backdrop-filter: blur(4.1px);
  background: rgba(255, 255, 255, 0.14);
  border: 1px solid rgba(255, 255, 255, 0.23);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-flow: column wrap;
  grid-gap: 20px;
  gap: 20px;
  height: 30rem;
  justify-content: center;
  width: 26rem;
}

.login img {
  margin-bottom: 30px;
}

.login input {
  border: none;
  border-radius: 3px;
  height: 2.8em;
  outline: none;
  padding-left: 10px;
  width: 22rem;
}

label h5 {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 6px;
}

.logo {
  height: 10vmin;
  pointer-events: none;
}

footer {
  align-items: center;
  background-color: transparent;
  bottom: 0;
  color: white;
  display: flex;
  height: 50px;
  justify-content: center;
  letter-spacing: 1.2px;
  line-height: 15px;
  position: fixed;
  text-align: center;
  width: 100%;
}

footer p {
  font-size: 10px;
  font-weight: 200;
}

.loading {
  align-items: center;
  background: -webkit-gradient(linear, left bottom, left top, from(rgb(20, 61, 195)), to(rgb(140, 119, 235)));
  background: linear-gradient(to top, rgb(20, 61, 195), rgb(140, 119, 235));
  color: white;
  display: flex;
  font-size: 20px;
  height: 100vh;
  justify-content: center;
  letter-spacing: 2px;
  overflow: hidden;
  width: 100vw;
}

header {
  align-items: center;
  background: rgba(218, 218, 218, 0.2);
  border-bottom: 1px solid rgba(216, 216, 216, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.15);
  display: flex;
  height: 6vh;
  justify-content: space-between;
  position: fixed;
  width: 100vw;
}

.header__container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: auto;
  width: 100%;
}

.header__left {
  align-items: center;
  display: flex;
  justify-content: center;
}

.header__left img {
  height: 4vmin;
  margin-right: 26px;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

.header__right {
  align-items: center;
  display: flex;
  justify-content: center;
}

.header__right img {
  border: 3px solid white;
  border-radius: 50%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
  height: 4vh;
  margin-left: 20px;
}

.header__left img:hover {
  height: 4.4vmin;
}

header span {
  color: rgb(12, 12, 12);
  font-size: 14px;
  font-weight: 800;
}

.game__container {
  align-items: center;
  background: -webkit-gradient(linear, left bottom, left top, from(rgb(20, 61, 195)), to(rgb(140, 119, 235)));
  background: linear-gradient(to top, rgb(20, 61, 195), rgb(140, 119, 235));
  color: white;
  display: flex;
  height: 100vh;
  justify-content: center;
  overflow: hidden;
  overflow-y: hidden;
  width: 100vw;
}

.game__container button {
  font-size: 14px;
  font-weight: 300;
}

.game__headings {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 220px;
  justify-content: center;
  margin-top: -140px;
  width: 100%;
}

.game__headings h4 {
  background: rgba(190, 190, 190, 0.2);
  border-radius: 4px;
  color: rgb(255, 216, 109);
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 1.4px;
  margin-bottom: 1.2em;
  padding-block: 0.6em;
  padding-inline: 4em;
  text-align: center;
}

.game__headings p {
  font-size: 32px;
  font-weight: 200;
  text-align: center;
  width: 60%;
}

.game__alternatives {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  margin: auto;
  width: 45em;
}

.game__alternatives button {
  background-color: rgb(38, 0, 99);
  border: 3px solid white;
  color: white;
  font-weight: 800;
}

.game__alternatives button:disabled {
  background: rgb(160, 160, 160);
  color: rgb(75, 75, 75);
  opacity: 0.6;
}

.game__options {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  height: 10rem;
  justify-content: center;
}

.next-question-btn {
  background: transparent;
  height: 20px;
  text-align: left;
  text-decoration: underline;
  width: auto;
}

.answers-reveal [ data-testid = 'correct-answer' ] {
  border: 3px solid rgb(6, 240, 15);
}

.answers-reveal [ data-testid ^= 'wrong-answer-' ] {
  border: 3px solid red;
}

.feedback__container {
  align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(175, 255, 215)), to(rgb(33, 23, 167)));
  background: linear-gradient(to bottom, rgb(175, 255, 215), rgb(33, 23, 167));
  color: white;
  display: flex;
  height: 100vh;
  justify-content: center;
  overflow: hidden;
  width: 100vw;
}

.feedback__container section {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;

}

.feedback__container div {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  font-weight: 300;
  grid-gap: 6px;
  gap: 6px;
  justify-content: center;
  margin-bottom: 20px;
}

.feedback__text {
  align-items: center;
  display: flex;
  font-weight: 300;
  justify-content: center;
  letter-spacing: 1.4px;
  text-align: center;
  width: 100%;
}

.feedback__text span {
  color: rgb(0, 45, 143);
  font-weight: 900;
}

.btn-play-again {
  background-color: transparent;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
  margin-top: -30px;
  outline: 3px solid rgb(0, 255, 200);
}

.btn-play-again:hover {
  color: rgb(0, 255, 200);
}

.ranking__container {
  align-items: center;
  background: -webkit-gradient(linear, left bottom, left top, from(rgb(20, 61, 195)), to(rgb(140, 119, 235)));
  background: linear-gradient(to top, rgb(20, 61, 195), rgb(140, 119, 235));
  color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  overflow: hidden;
  overflow-y: hidden;
  width: 100vw;
}

.ranking {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 80vh;
  justify-content: center;
  min-width: 480px;
  padding-top: 60px;
}

.ranking h2 {
  margin-top: 0.8em;
}

.ranking__container div {
  margin-top: 10px;
  overflow-x: hidden;
}

.ranking__container button {
  margin-top: 20px;
}

.ranking__container img {
  height: 40px;
}

.ranking__item {
  align-items: center;
  display: flex;
  justify-content: center;
}

.ranking__item img {
  border-radius: 50%;
  padding-inline: 20px;
}

.ranking__container p {
  padding-inline: 80px;
}

