header {
  align-items: center;
  background: rgba(218, 218, 218, 0.2);
  border-bottom: 1px solid rgba(216, 216, 216, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.15);
  display: flex;
  height: 6vh;
  justify-content: space-between;
  position: fixed;
  width: 100vw;
}

.header__container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: auto;
  width: 100%;
}

.header__left {
  align-items: center;
  display: flex;
  justify-content: center;
}

.header__left img {
  height: 4vmin;
  margin-right: 26px;
  transition: 0.3s ease;
}

.header__right {
  align-items: center;
  display: flex;
  justify-content: center;
}

.header__right img {
  border: 3px solid white;
  border-radius: 50%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
  height: 4vh;
  margin-left: 20px;
}

.header__left img:hover {
  height: 4.4vmin;
}

header span {
  color: rgb(12, 12, 12);
  font-size: 14px;
  font-weight: 800;
}
